/** @jsx React.DOM */
var Component = React.createClass({displayName: 'Component',

  getDefaultProps: function () {
    return {
      id: 'xxx',
      tags: [],
      baseHref: '/',
      tagsBaseHref: '/tags/',
      imageUrl: 'https://placeimg.com/1000/200/nature'
    }
  },

  render: function () {
    return (
      React.DOM.article({className: "visual-postlet", style: {backgroundImage: ['url(', this.props.imageUrl, ')'].join('')}}, 
        React.DOM.h2({className: "visual-postlet__permalink-wrap"}, React.DOM.a({href: this.props.baseHref + 'EV-' + this.props.id, className: "visual-postlet__permalink"}, "EV-",  this.props.id)), 
        React.DOM.nav({className: "block-nav block-nav--tags block-nav--hover-invert"}, 
        this.props.tags.map(function (tag) {
          return React.DOM.a({href: this.props.tagsBaseHref + tag, className: "block-nav__link active"}, React.DOM.span({className: "block-nav__link-text"}, "#", tag));
        }.bind(this))
        )
      )
    );
  }

});

module.exports = Component;
